*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.background{
    padding-top: 40px;
    padding-bottom: 40px;
    margin-top: 30px;
    .control{
        .headerText{
            display: flex;
            align-items: center;
            justify-content: center;
            h2{
                display: flex;
                align-items: center;
                gap: 5px;
                font-size: 32px;
                font-weight: 600;
                h1{
                font-size: 32px;
                color: #6E4EEF;
                font-weight: 600;
                }
            }
        }
        .boxOne{
          margin-top: 80px;
            display: flex;
            justify-content: space-between;
            
           .leftBox{
            width: 40%;
               h1{
                font-size: 32px;
                font-weight: 600;
               }
               .controlLeftBox{
                margin-top: 30px;
                background-color: #DFEED7;
                padding: 30px;
                border-radius: 10px;
                display: flex;
                justify-content: space-between;
                .leftBoxText{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    gap: 100px;
                    .leftBoxHeader{
                        h2{
                            color: #439A75;
                        }
                    }
                    .leftBoxBottom{
                        display: flex;
                        flex-direction: column;
                        gap: 10px;
                        h2{
                            font-size: 28px;
                            font-weight: 600;
                          
                        }p{
                            line-height: 26px;
                            width: 400px;
                        }
                    }
                }
               }
           } 
           .controlMiddleBox{
            background-color: #F9E8D6;
            width: 28%;
            padding: 30px;
            border-radius: 10px;
            .middleBoxText{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                gap: 108px;
                .middleBoxHeader{
                    h2{
                        color: #ED8E61;
                    }
                }
                .middleBoxBottom{
                    display: flex;
                    flex-direction: column;
                    gap: 28px;
                    h2{
                        font-size: 28px;
                        font-weight: 600;
                      
                    }p{
                        max-width: 400px;
                        line-height: 22px;
                    }
                }
            }
           }
           .controlRightBox{
            background-color: #E6DEEB;
            width: 28%;
            padding: 30px;
            border-radius: 10px;
            .rightBoxText{
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                gap: 108px;
                .rightBoxHeader{
                    h2{
                        color: #A68CD0;
                    }
                }
                .rightBoxBottom{
                    display: flex;
                    flex-direction: column;
                    gap: 28px;
                    h2{
                        font-size: 28px;
                        font-weight: 600;
                      
                    }p{
                        max-width: 400px;
                        line-height: 22px;
                    }
                }
            }
           }
        }
        .boxTwo{
            display: flex;
            justify-content: space-between;
            margin-top: 17px;
            gap: 17px;
            .controlFiveBox{
    
                width: 100%;
                background-color: #DFEED7;
                padding: 30px;
                border-radius: 10px;
                display: flex;
                justify-content: space-between;
                .fiveBoxText{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    gap: 100px;
                    .fiveBoxHeader{
                        h2{
                            color: #439A75;
                        }
                    }
                    .fiveBoxBottom{
                        display: flex;
                        flex-direction: column;
                        gap: 10px;
                        h2{
                            font-size: 28px;
                            font-weight: 600;
                          
                        }p{
                            width: 800px;
                            line-height: 26px;
                        }
                    }
                }
               }
               .controlForBox{
                background-color: #E6DEEB;
                width: 24%;
                padding: 30px;
                border-radius: 10px;
                .forBox{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
        
                    .forBoxHeader{
                        h2{
                            color: #A68CD0;
                        }
                    }
                    .forBoxBottom{
                        display: flex;
                        flex-direction: column;
                        gap: 28px;
                        h2{
                            text-align: center;
                            font-size: 28px;
                            font-weight: 600;
                          
                        }p{
                            max-width: 400px;
                            line-height: 22px;
                        }
                    }
                }
               }
    
        }
        .boxThree{
            display: flex;
            justify-content: space-between;
            margin-top: 17px;
            gap: 17px;
            .controlFiveBox{
    
                width: 100%;
                background-color: #F8F0D2;
                padding: 30px;
                border-radius: 10px;
                display: flex;
                justify-content: space-between;
                .fiveBoxText{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    gap: 100px;
                    .fiveBoxHeader{
                        h2{
                            color: #439A75;
                        }
                    }
                    .fiveBoxBottom{
                        display: flex;
                        flex-direction: column;
                        gap: 10px;
                        h2{
                            font-size: 28px;
                            font-weight: 600;
                          
                        }p{
                            width: 800px;
                            line-height: 26px;
                        }
                    }
                }
               }
               .controlForBox{
                background-color: #E6DEEB;
                width: 24%;
                padding: 30px;
                border-radius: 10px;
                .forBox{
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
        
                    .forBoxHeader{
                        h2{
                            color: #A68CD0;
                        }
                    }
                    .forBoxBottom{
                        display: flex;
                        flex-direction: column;
                        gap: 28px;
                        h2{
                            text-align: center;
                            font-size: 28px;
                            font-weight: 600;
                          
                        }p{
                            max-width: 400px;
                            line-height: 22px;
                        }
                    }
                }
               }
    
        }
    
    
    }
}

